.page{
  font-family: Roboto, sans-serif;
  font-weight: 400;
  size: 16px;
  color: $main-text-color;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;

    &:target{
      opacity: 1;
      pointer-events: all;
    }
  }
}

