.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__title  {
    margin: 0;
    color: #FFF;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__bottom {
    padding-bottom: 40px;
    @include contentPaddingInline;
    margin: 0;

    @include onTablet{
      padding-bottom: 64px;
    }
  }

}
